.hero {
	transform-style: preserv-3d;
	position: relative;
	filter: brightness(40%);
	// background: url("../img/hero2.jpg") center
	// background-size: cover
	height: 100vh;
	&:before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		z-index: -1;
		transform-origin: center;
		min-height: 100vh;
		background: url("../img/hero2.jpg") center;
		background-size: cover;
		transform: translateZ(-1px); }
	&__title {
		font-size: 8rem;
		position: absolute;
		top: 20%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		width: 100%; }
	&__subtitle {
		font-size: 5rem;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		width: 100%; }
	&__wrapper {
		text-align: center;
		text-transform: uppercase;
		transform-style: inherit; } }
